<template>
  <video controls="" autoplay="" loop="" name="media" :data-aos="getType">
    <source :src="getSrc" type="video/mp4">
  </video>
  <!-- <vue-plyr>
    <video :src="getSrc">
      <track kind="captions" label="English" srclang="en" src="captions-en.vtt" default>
    </video>
  </vue-plyr> -->
</template>

<script>

export default {
  name: 'ConVideo',
  props: {
    src: String,
    type: String,
  },
  computed: {
    getSrc() {
      return this.src;
    },
    getType() {
      return this.type;
    },
  }
}
</script>

<style>
</style>



